
















































	var self;
    import service from '../../service.js';
    import mInput from '../../components/m-input.vue'
	
    export default {
        components: {
            mInput
        },
        data() {
			lists:[],
			self=this
            return {
				code_url:'http://192.168.0.199:8080/agent/login/captcha?',
				disableScroll:true,
                providerList: [],
                hasProvider: false,
                account: '',
                password: '',
                positionTop: 0,
				code:'',
				
				random_num:0,
            }
        },
		onLoad:function(){
			this.random_num=new Date().getTime();
			console.log('random_num'+this.random_num);
			this.changeImg();
		},
        methods: {
			//获取验证码
			changeImg(){
				this.code_url="";
				var rand= new Date().getTime();
				this.random_num= this.random_num+1;
				uni.request({
					url: 'http://192.168.0.199:8080/agent/login/captcha?refresh= ',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					method: 'POST',
					dataType: 'json',
					cache: false,
					data: {},
					success: res => {
						console.log(res);
						this.code_url='http://192.168.0.199:8080'+res.data['url'];
						console.log(res.data['url']);
						console.log(this.code_url)
					},
					fail: () => {},
					complete: () => {}
				});
			},
			//使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			// 反向使用 top 进行定位，可以避免此问题。
			initPosition() {
                this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
            },
            
			//登录
			bindLogin() {
                if (this.account.length < 4) {
                    uni.showToast({
                        icon: 'none',
                        title: '账号最短为 4 个字符'
                    });
                    return;
                }
                // if (this.password.length < 4) {
                //     uni.showToast({
                //         icon: 'none',
                //         title: '密码最短为 4 个字符'
                //     });
                //     return;
                // }
				uni.request({
					url: 'http://192.168.0.199:8080/agent/login/ajax-login',//
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					method: 'POST',
					dataType: 'json',
					cache: false,
					data: {
						account:this.account,
						password:this.password,
						code:this.code,
					},
					success: res => {
						console.log(res)
						if(res.data.isSuccess==200){
							//缓存
							uni.setStorage({
								key: 'agentInfo',
								data: {
								id:res.data.result.id,
								agent_id:res.data.result.agent_id,
							    agent_tel:res.data.result.agent_tel,
								herf_pic:res.data.result.herf_pic,
								nickname:res.data.result.nickname,
								token:res.data.result.token,
							},
								success: function () {
									uni.redirectTo({
										url: '../tabbar/tabbar'
									});
								}
							});
						}else{
							uni.showToast({
							    icon: 'none',
							   title: res.data.message
							});
						}
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					},
					complete: () => {}
				});
            },
			login(){
				uni.request({
					url: 'http://192.168.0.199/agent/login/ajax-login',
					method: 'POST',
					dataType: 'json',
					cache: false,
					data: {
						username:this.account,
						pwd:this.password,
						verify_code:this.code,
					},
					success: res => {
						console.log(res)
						if(res.data.isSuccess==200){
							uni.showToast({
							    icon: 'none',
							    title: res.data.message
							});
							
							
							
						}else{
							uni.showToast({
							    icon: 'none',
							   title: res.data.message
							});
						}
					},
					fail: () => {
						
					},
					complete: () => {}
				});
			},
            toMain(userName) {
                this.login(userName);
                /**
                 * 强制登录时使用reLaunch方式跳转过来
                 * 返回首页也使用reLaunch方式
                 */
                if (this.forcedLogin) {
                    // uni.reLaunch({
                    //     url: '../main/main',
                    // });
					uni.redirectTo({
						url: '../index/index?account='+this.account
					});
                } else {
                    uni.navigateBack();
                }

            }
        },
        onReady() {
            this.initPosition();
        }
    }
